* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.body {
  display: flex;
}

.navbar {
  background-color: #f2f2f2;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1rem 2rem;
  display: flex;
}

.navbar__search {
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 1rem;
  display: flex;
}

.low-stock {
  color: red;
}

.main__product-list {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  padding: 2rem;
  display: flex;
}

.product-card {
  border: 1px solid #f2f2f2;
  border-radius: 5px;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 300px;
  height: 600px;
  margin: 1rem;
  padding: 1rem;
  display: flex;
  box-shadow: 0 0 10px #0000001a;
}

.product-card:hover {
  transform: matrix(1.05, 0, 0, 1.05, 0, 0);
  box-shadow: 0 0 10px #00000080;
}

.product-image {
  object-fit: cover;
  width: 100%;
  height: 100%;
  max-height: 250px;
}

.btn {
  cursor: pointer;
  color: #e2e2e2;
  background-color: #303030;
  border: none;
  border-radius: 5px;
  margin: .5rem;
  padding: .5rem 1rem;
}

.btn:hover {
  color: #303030;
  background-color: #e2e2e2;
}
